<template>
  <div class="lg-export-excel">
	<el-dialog title="" width="30%" :visible.sync="isShow" :before-close="handleClose">
	  <el-form ref="form" :model="form" label-width="80px">
		  <el-form-item label=":">
		    <el-input v-model="form.name" placeholder="(excel-list)"></el-input>
		  </el-form-item>
		  <el-form-item label=":">
		    <el-radio-group v-model="form.type">
		      <el-radio label="xlsx"></el-radio>
		      <el-radio label="csv"></el-radio>
		    </el-radio-group>
		  </el-form-item>
	  </el-form>
	  <span slot="footer" class="dialog-footer">
	    <el-button @click="handleCancel"> </el-button>
	    <el-button type="primary" @click="handleDownload"> </el-button>
	  </span>
	</el-dialog>
  </div>
</template>

<script>
export default {
  name: 'LgExportExcel',
  props: {
    isShow: {
      type: Boolean,
      required: true
    },
    onClose: {
      type: Function,
      required: true
    }
  },
  data() {
    return {
      form: {
        name: '',
        type: 'xlsx'
      },
      list: [{
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          date: '2016-05-03'          
        }, {
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          date: '2016-05-02'          
        }, {
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          date: '2016-05-04'          
        }, {
          name: '',
          province: '',
          city: '',
          address: ' 1518 ',
          date: '2016-05-01'          
        }]
    };
  },
  methods: {
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => {
        if (j === 'timestamp') {
          return this.parseTime(v[j])
        } else {
          return v[j]
        }
      }))
    },
    handleDownload() {
      import('./Export2Excel.js').then(excel => {
        const tHeader = ['name', 'province', 'city', 'address', 'date']
        const filterVal = ['name', 'province', 'city', 'address', 'date']
        const list = this.list
        const data = this.formatJson(filterVal, list)
        excel.export_json_to_excel({
          header: tHeader,
          data,
          filename: this.form.name,
          bookType: this.form.type,
          autoWidth: false
        })
      });
      this.onClose();
    },
    handleCancel() {
      this.onClose();
    },
    handleClose() {
      this.onClose();
    },
    parseTime(time, cFormat) {
      if (arguments.length === 0) {
        return null
      }
      const format = cFormat || '{y}-{m}-{d} {h}:{i}:{s}'
      let date
      if (typeof time === 'object') {
        date = time
      } else {
        if (('' + time).length === 10) time = parseInt(time) * 1000
        date = new Date(time)
      }
      const formatObj = {
        y: date.getFullYear(),
        m: date.getMonth() + 1,
        d: date.getDate(),
        h: date.getHours(),
        i: date.getMinutes(),
        s: date.getSeconds(),
        a: date.getDay()
      }
      const time_str = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
        let value = formatObj[key]
        // Note: getDay() returns 0 on Sunday
        if (key === 'a') { return ['', '', '', '', '', '', ''][value ] }
        if (result.length > 0 && value < 10) {
          value = '0' + value
        }
        return value || 0
      })
      return time_str
    }
  }
};
</script>

<style>

</style>



